$web-primary: #AA0601;

/*顏色設定*/ 
$font-primary-color:#650300;
$font-secondary-color:#fff;
$font-third-color:#AA0601;
$font-paragraph:#000;
$font-hover:#ffb100;

/*文字設定*/ 
$font-size-base:1rem; //16px
$h-lg-font-size: $font-size-base*4.5; //72px
$h1-font-size: $font-size-base*4; //64px
$h2-font-siz: $font-size-base*3; //48px
$h3-font-size: $font-size-base*2.25; //36px
$h4-font-size: $font-size-base*1.75; //28px
$h5-font-size: $font-size-base*1.5; //24px
$h6-font-size: $font-size-base*1.25; //20px

$font-primary-weight:bold;

/*字體設定*/ 
$font-noto: 'Noto Sans TC', sans-serif;
$font-roboto: 'Roboto', sans-serif;
$font-icon:'Material Icons Outlined';

/*行高設定*/
$line-height-base: 1.5rem;

/*padding設定*/
$padding-top-80: 5rem;

/*匯入設定*/ 
@import "reset.css";


/*mixin設定*/ 
@mixin hide-text{
  text-indent:101%;
  white-space: nowrap;
  overflow: hidden;
}

@mixin h2-text-padding{
  font-size: 48px;
  font-weight: bold;
  padding-top: 60px;
  padding-bottom: 48px;
  text-align: center;
}




/*RWD設定*/ 
body{
  font-family: $font-noto;
}
img{
  max-width: 100%;
  height: auto;
  display: block;
}

.container{
  width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}



.header {
  background-color: $web-primary;
  color: $font-secondary-color;
  font-size: $h6-font-size;
  display: flex;
  justify-content: space-between;
  padding: 30px 88px;
  .menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
     li a{
      padding-right: 64px;
      padding-top: 8px;
      padding-bottom: 8px;
      text-decoration: none;
      display: block;
      color: $font-secondary-color;
      &:hover{
        color: $font-hover;
        transition: all 0.8s;
      }
    }  
}
}
.menu li:last-child a{
  padding-right: 0;
}

.logo {
  width: 80px;
  height:49px;
  &:hover{
    opacity: 0.5;
    transition: all 0.5s;
  }
}



.banner{
  background: url(../images/banner.png) no-repeat center;
  height: 840px;
  color: $font-primary-color;
  .container{
    display: flex;
    justify-content:center;
    flex-direction: column;
    text-align: right;
    height: 100%;
  }
  .text-sm{
    font-size: $h3-font-size;
    margin-bottom: 16px;
  }
  .text-lg{
    font-weight: $font-primary-weight;
    font-size: $h1-font-size;
  }
}

.major{
  background: #FBF2F2;
  padding-bottom: $padding-top-80;
   h2{
    @include h2-text-padding
    color:$font-primary-color;
  }
}

.major-items{
  display: flex;
  justify-content: space-between;
  text-align: center;
  li{
    width: 23%;
  }
  .material-icons-outlined{
    font-size: 84px;
    color: $font-primary-color;
    display: block;
    margin-bottom: 24px;
  }
  h5{
    color: $font-primary-color;
    font-size: $h5-font-size;
    text-align: center;
    font-weight: bold;
    margin-bottom: 8px;
  }
  p{
    line-height: $line-height-base;
    text-align: left;
  }

}



.classic{
  padding-bottom: 32px;
  h2{
    @include h2-text-padding
    color:$font-primary-color;
  }
}



.classic-items li{
  display: flex;
  justify-content: space-between;
  align-items: center;
  .series-img{
    width: 58%;
    max-width: 635px;
    margin-bottom: 32px;
    &:hover{
      opacity: 0.5;
      transition: all 0.8s;
    }
  }
  .text-title{
    width: 42%;
    font-size: $h2-font-siz;
    color: $font-third-color;
    font-weight: bold;
    font-style: italic;
  }
  
}
.classic-items li{
  .material-icons-outlined{
    font-size: 44px;
    color: $font-third-color;
    display: inline-block;
   
  }
  &:nth-child(2){
    flex-direction: row-reverse;
  text-align: right;
  }
}

.co-design{
  background: url(../images/co-bg.png) no-repeat center;
  height: 705px;
  h2{
    color:$font-secondary-color;
    @include h2-text-padding
  }
}

.co-design-items{
  display: flex;
  justify-content:space-between;
  li{
      width: 47.5%;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
  } 
  .co-design-title{
    font-size: $h-lg-font-size;
    color:$font-third-color;
    font-weight: bold;
    position: absolute;
  
  }

}
.co-design-items{
  .co-img {
    &:before {
      content: "";
      display: block;
      background: #fbf2f273;
      width: 100%;
      height: 100%;
      position: absolute;
  }
  }
  }


.co-design-items .co-img:hover.co-img::before {
      background: none;
      transition: ease-in-out 0.8s;
      cursor: pointer;
  }
  
  










.footer {
  background-color: $web-primary;
  color: $font-secondary-color;
  
}



